import Vue from 'vue';
let qs = require('qs');
// const $http = '/api/open/user'
const $http = ''
export default {     
    loginCheck(param){
        return Vue.axios.post('/user'+$http+'/loginCheck',qs.stringify(param));
    },
    logout(param){
        return Vue.axios.post('/user'+$http+'/quit');
    },
    getLogin(param){
        return Vue.axios.post('/user'+$http+'/signin/passwd',param);
    },
    getImgCode(param){
        return Vue.axios.post('/user'+$http+'/imgcode/get',qs.stringify(param));
    },
    getPhoneCode(param){
        return Vue.axios.post('/user'+$http+'/vcode/get',param);
    },
    reg(param){
        return Vue.axios.post('/user'+$http+'/signup/passwd',param);
    },
    forgetPasswd(param){
        return Vue.axios.post('/user'+$http+'/passwd/forget',param);
    },
    resetPasswd(param){
        return Vue.axios.post('/user'+$http+'/passwd/reset',param);
    },
    verifyMobile(param){
        return Vue.axios.post('/user'+$http+'/mobile/verify',param);
    },
    getInfo(param){
        return Vue.axios.post('/user'+$http+'/info/get',param);
    },
    setInfo(param){
        return Vue.axios.post('/user'+$http+'/info/set',param);
    },
    modifyEmail(param){
        return Vue.axios.post('/user'+$http+'/email/modify',param);
    },
    verifyEmail(param){
      return Vue.axios.post('/user'+$http+'/email/verify',param);
  },
    applyDeveloper(param){//申请成为个人开发者
      return  Vue.axios.post('/user'+$http+'/personal/auth',param)
    },
    applyResult(param){//个人开发者信息和审核结果
      return  Vue.axios.post('/user'+$http+'/personal/get')
    },
    apikeyReset(param){//重置apikey
      return  Vue.axios.post('/user'+$http+'/apikey/reset')
    },
    GetTokenqiniu(param){//获取七牛云token
      return  Vue.axios.post('/user'+$http+'/qiniu/get')
    },
    GetapplyResult(param){//获取七牛云token
      return  Vue.axios.post('/user'+$http+'/personal/get')
    }
    


    
}


